:root {
  --bp-lg: 70em;
  --bp-md: 50em;
  --bp-sm: 30em;
}

html,
body {
  background-color: var(--white);
  box-sizing: border-box;
  color: #444;
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  margin: 0;
  padding: 0;
  position: relative;
  scroll-behavior: smooth;
}

header {
  background-color: #28324A;
  background-image: url("http://res.cloudinary.com/frndcolin/image/upload/w_500,h_700,dpr_auto,c_fill,g_faces:center/v1519797186/wedding-site/img015.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  margin: 0;
  padding: 0;
  position: relative;
  height: 100vh;
  max-height: calc((9 / 16) * 100%);
  width: 100%;
  overflow: hidden;
  z-index: 888;
}

main {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
}

section {
  margin: 0;
  min-height: 75vh;
  padding: 0;
  width: 100%;
  position: relative;
}

h1 {
  font-size: calc(54px + (100 - 54) * ((100vw - 300px) / (1600 - 300)));
  font-family: 'Playfair Display', serif;
  font-weight: 200;
  text-transform: uppercase;
  margin: 0 auto;
  position: relative;
  width: 100vw;
}

h2 {
  font-family: 'Great Vibes', cursive;
  font-size: 60px;
  font-size: calc(54px + (80 - 54) * ((100vw - 300px) / (1400 - 300)));
  font-weight: 400;
  margin: 0 0 15px 0;
  text-align: center;
}

p {
  line-height: 1.318;
  margin: 0 auto;
  max-width: 700px;
  text-align: center;
  width: 95%;
}

a {
  color: inherit;
  text-decoration: none;
  text-transform: inherit;
  font-size: inherit;
  font-weight: inherit;
  position: relative;
}

@media only screen and (min-width: 30em) {
  header {
    background-image: url("http://res.cloudinary.com/frndcolin/image/upload/w_800,h_500,dpr_auto,c_fill,g_faces:center/v1519797186/wedding-site/img015.jpg");
    background-position: center right;
  }
}

@media only screen and (min-width: 600px) {
  header {
    background-image: url("http://res.cloudinary.com/frndcolin/image/upload/q_100/v1519797186/wedding-site/img015.jpg");
    background-position: center bottom;
  }

  .grid--2-col  p {
    max-width: 70%;
  }
}

@media only screen and (min-width:70em) {
  body {
    font-size: 18px;
    font-weight: 400;
  }
}

@media only screen and (min-width: 95em) {
  body {
    font-size: 20px;
  }
}

#canvas-image-blending {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  text-align: center;
}

.grid--2-col {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: "lft" "rgt";
  min-height: 480px;
}

.grid-item--lft {
  grid-area: lft;
}

.grid-item--rgt {
  grid-area: rgt;
}

@media only screen and (min-width: 50em) {
  .grid--2-col {
    grid-template-columns: 50% 50%;
    grid-template-rows: 1fr;
    grid-template-areas: "lft rgt";
  }
}

.locations {
  width: 100%;
  height: 400px;
  position: relative;
  overflow: hidden;
}

#map {
  height: 400px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}

#photographs .container {
  padding-top: 60px;
}

.gallery {
  display: grid;
  grid-template-columns: 95%;
  grid-auto-rows: 50vw;
  grid-gap: 10px;
  grid-auto-flow: dense;
  justify-content: center;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 95vw;
  max-width: ;
}

.gallery-item {
  background-color: var(--black);
  box-shadow: -1px 2px 4px rgba(100, 100, 100, 0.6);
  overflow: hidden;
  border: 2px solid var(--white);
  border-radius: 2px;
}

.gallery-item img {
  width: 100%;
  height: 100%;
  opacity: 0.8;
  object-fit: cover;
  transform: scale(1.1);
  transition: all 0.3s;
}

.gallery-item img:hover {
  opacity: 0.95;
  transform: scale(1);
}

.img-lg,
.img-tl,
.img-sm {
  grid-column: span 1;
  grid-row: span 1;
}

@media only screen and (min-width: 40em) {
  .gallery {
    grid-template-columns: repeat(auto-fill, 20vw);
    grid-gap: 12px;
    grid-auto-rows: 20vw;
  }

  .img-lg {
    grid-column: span 2;
    grid-row: span 2;
  }

  .img-tl {
    grid-column: span 1;
    grid-row: span 2;
  }

  .img-sm {
    grid-column: span 1;
    grid-row: span 1;
  }
}

@media only screen and (min-width: 70em) {
  .gallery {
    grid-template-columns: repeat(auto-fill, 250px);
    grid-gap: 6px;
    grid-auto-rows: 150px;
    max-width: 1200px;
  }
}

@media only screen and (min-width:90em) {
  .container {
    min-height: 500px;
  }

  .locations,
  #map {
    height: 450px;
  }
}

.menu {
  background-color: var(--blue);
  font-size: 1rem;
  left: 0;
  position: fixed;
  top: -110%;
  width: 100%;
  z-index: 700;
  transition: all 0.3s;
  height: 100vh;
}

.nav-btn {
  border-radius: 5px;
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 15px;
  top: 10px;
  z-index: 999;
  transition: all 0.5s;
}

.nav-btn img {
  width: 100%;
  height: auto;
}

.menu.open {
  top: 0;
}

.nav-btn.pressed {
  width: 40px;
  height: 40px;
  position: fixed;
  background-color: var(--blue);
  background-image: url("../img/close.svg");
  background-size: contain;
}

.nav-btn.pressed img {
  opacity: 0;
}

.main-nav {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 50px 0 0;
  flex-direction: column;
  justify-content: space-around;
  height: 80vh;
}

.main-nav .nav-item {
  padding: 5px;
  margin: 0 7px;
  text-align: center;
}

.nav-item {
  color: var(--white);
  display: inline-block;
  margin: 0 6px;
  position: relative;
  text-transform: uppercase;
}

.nav-item a::after {
  content: '';
  display: block;
  height: 2px;
  width: 100%;
  background-color: var(--gold);
  position: absolute;
  left: 0;
  bottom: -4px;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease-out;
}

.nav-item a:hover::after,
.nav-item a:active::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

@media only screen and (min-width: 55em) {
  .menu {
    background-color: transparent;
    position: absolute;
    top: 0;
  }

  .main-nav {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 80px;
  }

  .nav-btn {
    display: none;
  }
}

/*-----------------------------------------------------------------------*/

/*-------------------------GENERAL ELMNTS/STYLES-------------------------*/

/*-----------------------------------------------------------------------*/

:root {
  --blue: #28324A;
  --gold: #D6A319;
  --dark-gold: #CC9C1A;
  --black: #444444;
  --white: #FFFFFF;
}

.btn {
  background-color: var(--gold);
  border: 2px solid var(--gold);
  border-radius: 2px;
  color: var(--white);
  font-size: 1rem;
  font-weight: 400;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
  margin: 40px auto 0;
  padding: 13px;
  min-width: 175px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s;
}

.btn-ghost {
  background-color: transparent;
  color: var(--gold);
  padding: 13px;
  margin-top: 10px;
}

.btn:hover {
  background-color: var(--dark-gold);
  color: var(--white);
}

.btn-group {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  margin: 40px auto 0;
}

.btn-group .btn {
  margin: 5px 10px;
}

.hide {
  display: none !important;
}

.show {
  display: block;
}

.map-hide {
  overflow: hidden;
  height: 0;
}

.map-show {
  display: flex;
  height: 100%;
}

@media only screen and (min-width: 30em) {
  .btn-group {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
  }

  .btn-group .btn {
    margin: 0 10px;
  }
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------HEADER------------------------------*/

/*-----------------------------------------------------------------------*/

header:before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(40, 50, 75, 0.1)), linear-gradient(rgba(214, 163, 25, 0.25), rgba(40, 50, 75, 0.05), rgba(40, 50, 75, 0.6));
}

.names {
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  text-shadow: 0 0 18px rgba(40, 50, 75, 0.32);
  transform: translateY(-50%);
}

.and {
  color: var(--gold);
  font-size: 1.3em;
  line-height: 0.5;
  margin: 5px -12px 5px -10px;
  display: block;
}

.full-date {
  color: var(--white);
  font-size: 20px;
  font-size: calc(20px + (28 - 20) * ((100vw - 360px) / (1600 - 360)));
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  min-width: 250px;
  max-width: 330px;
  margin-top: 40px;
}

.full-date::before {
  display: block;
  content: '';
  width: 50%;
  height: 1px;
  background-color: var(--white);
  position: absolute;
  left: -53%;
  top: 0.55em;
}

.full-date::after {
  display: block;
  content: '';
  width: 50%;
  height: 1px;
  background-color: var(--white);
  position: absolute;
  right: -52%;
  top: 0.55em;
}

@media only screen and (min-width: 40em) {
  .and {
    display: inline-block;
  }

  .full-date {
    margin-top: 15px;
  }
}

/*-----------------------------------------------------------------------*/

/*                                   WELCOME                             */

/*-----------------------------------------------------------------------*/

.countdown {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.date {
  border-radius: 2px;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 30px 1fr;
  justify-items: center;
  width: 100px;
  height: 95px;
  background-color: var(--blue);
  margin: 20px 10px 0;
  box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.6), 2px 2px 12px rgba(0, 0, 0, 0.5);
}

.date-title {
  color: var(--white);
  align-self: end;
  font-size: 16px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
}

[class*="date--"] {
  font-family: serif;
  font-size: 48px;
  align-self: center;
  color: var(--gold);
}

.hours,
.minutes,
.seconds {
  display: none;
}

@media only screen and (min-width: 30em) {
  .hours {
    display: grid;
  }
}

@media only screen and (min-width: 44em) {
  .minutes,
  .seconds {
    display: grid;
  }
}

@media only screen and (min-width: 90em) {
  .date {
    height: 110px;
    width: 110px;
  }

  .date-title {
    font-size: 18px;
  }

  [class*="date--"] {
    font-size: 64px;
  }
}

.playlist {
  box-shadow: -2px 3px 12px #888;
  position: absolute;
  background-color: var(--gold);
  top: 5%;
  width: 320px;
  overflow: hidden;
  transform: translateY(-50%);
  padding: 20px 0 0 30px;
  right: -285px;
  height: 40px;
  z-index: 700;
  transition: all 0.3s;
}

.playlist-btn {
  position: absolute;
  top: 10px;
  display: block;
  background-color: var(--gold);
  content: '';
  left: 7px;
  width: 50px;
  height: 50px;
  animation-iteration-count: infinite;
  background-size: 80% 80%;
  background-repeat: no-repeat;
  background-position: top left;
  background-image: url("../img/music.svg");
}

.playlist-body {
  height: 0;
  overflow: hidden;
  font-size: 20px;
  font-weight: 200;
  color: var(--white);
  padding: 0;
  width: 280px;
  margin: 30px auto 40px;
}

.playlist-body > h3 {
  font-size: inherit;
  font-weight: 200;
  text-align: center;
  line-height: 1.3;
}

.playlist-body > a {
  border: 1px solid var(--blue);
  background-color: var(--blue);
  display: block;
  padding: 12px;
  font-weight: 400;
  width: 180px;
  margin: 0 auto;
}

.playlist.opened {
  height: 270px;
  width: 330px;
  top: 53%;
  right: 0;
}

.playlist.opened > .playlist-body {
  height: auto;
}

@media only screen and (min-width: 50em) {
  .playlist {
    position: fixed;
    top: 35%;
  }
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------EVENT------------------------------*/

/*-----------------------------------------------------------------------*/

.event-content {
  color: var(--white);
  text-align: center;
  background-color: var(--blue);
  font-weight: 200;
}

.event-img {
  width: 100%;
  height: 100%;
  display: none;
}

.event-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media only screen and (min-width: 50em) {
  .event-img {
    display: block;
  }
}

.schedule {
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
}

.event {
  letter-spacing: 0.5px;
  margin: 12px 0;
}

.highlight {
  color: var(--gold);
  font-weight: 400;
  font-size: 1.15em;
}

/*-----------------------------------------------------------------------*/

/*-------------------------------Accommodations----------------------------*/

/*-----------------------------------------------------------------------*/

.discount {
  color: var(--gold);
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: underline;
}

.map-title {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 88;
  background-color: rgba(40, 50, 75, 0.6);
  color: var(--white);
  padding: 4px 10px;
}

.map-info {
  background-color: rgba(40, 50, 74, 0.85);
  color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  transition: all 0.4s;
  width: 100%;
  z-index: 99;
}

.map-info-body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  align-content: center;
  width: 60%;
  position: relative;
}

@media only screen and (max-width: 1100px) {
  .map-info-body {
    width: 80%;
    border: none;
    border-radius: 0;
  }

  .map-info-body p {
    max-width: 580px;
  }
}

@media only screen and (max-width: 700px) {
  .map-info-body {
    width: 100%;
  }
}

.map-info-close {
  background-image: url("../img/close.svg");
  background-size: contain;
  display: block;
  height: 40px;
  width: 40px;
  position: absolute;
  right: 15px;
  opacity: 0.7;
  top: 15px;
  z-index: 350;
  transition: all 0.3s;
}

.map-info-close:hover {
  transform: scale(1.2);
  opacity: 1;
}

.map-info-title h4 {
  font-family: 'Great Vibes', sans-serif;
  font-size: 38px;
  font-weight: 200;
  margin: 24px 0 12px;
  text-align: center;
}

.map-info-content {
  padding: 0 40px;
  display: flex;
  font-weight: 200;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .map-info-content {
    width: 100%;
    padding: 0;
  }
}

.map-btns {
  margin: 20px auto 25px;
}

.map-info-btn {
  display: inline-block;
  text-decoration: none;
  padding: 8px 32px;
  border-radius: 2px;
  border: 2px solid var(--gold);
  color: var(--gold);
  text-transform: uppercase;
  font-weight: 400;
  background-color: transparent;
  margin: 0 6px;
  transition: all 0.3s;
  min-width: 96px;
  text-align: center;
}

.map-info-btn:hover {
  background-color: var(--gold);
  color: var(--white);
}

@media only screen and (max-width: 440px) {
  .map-info-title {
    margin-top: 24px;
  }

  .map-info-btn {
    display: block;
    margin: 6px auto;
  }
}

/*-----------------------------------------------------------------------*/

/*-------------------------------THINGS TO DO----------------------------*/

/*-----------------------------------------------------------------------*/

#things-to-do .container {
  padding: 40px 0 !important;
}

.boston {
  font-weight: 200;
  color: var(--white);
  background-image: linear-gradient(rgba(40, 50, 74, 0.8), rgba(40, 50, 74, 0.9)), url("../img/boston.jpg");
  background-size: cover;
}

.cape-cod {
  font-weight: 200;
  color: var(--white);
  background-image: linear-gradient(rgba(214, 123, 25, 0.4), rgba(40, 50, 74, 0.8), rgba(40, 50, 74, 0.9)), url("../img/chatham.jpg");
  background-size: cover;
}

.todo-list {
  list-style: none;
  padding: 0;
  margin: 20px 0 0 0;
  height: 180px;
}

.todo-list li {
  margin: 8px 0;
  font-weight: 400;
}

.todo-list li a:link,
.todo-list li a:visited {
  padding: 4px;
  text-decoration: none;
  transition: all 0.1s;
}

.todo-list li a:hover,
.todo-list li a:active {
  font-size: 1.1em;
  color: var(--gold);
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------GALLERY-----------------------------*/

/*-----------------------------------------------------------------------*/

#photographs {
  background-color: #f3f5f9;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------REGISTRY----------------------------*/

/*-----------------------------------------------------------------------*/

.registry-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.registry {
  display: inline-block;
  margin: 0 40px;
}

.registry img {
  width: 250px;
  height: auto;
  transition: all 0.2s;
}

.registry img:hover {
  transform: scale(1.1);
}

footer {
  background-color: var(--white);
  background-image: linear-gradient(rgba(30, 40, 64, 0.85), rgba(30, 40, 64, 0.75)), url("http://res.cloudinary.com/frndcolin/image/upload/w_1420,dpr_auto,c_fill/v1519797186/wedding-site/squirrel.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  color: var(--white);
  padding: 40px 0 20px 0;
  position: relative;
}

form {
  margin: 5px auto;
  text-align: center;
  padding: 5px 7px 40px;
}

input {
  margin: 4px auto;
  font-size: 0.9em;
  color: var(--white);
  background-color: transparent;
  border: none;
  border-bottom: 2px solid var(--gold);
  border-left: 2px solid var(--gold);
  border-radius: 0;
  padding: 8px;
  letter-spacing: 1px;
  width: 90%;
}

input:focus {
  background-color: rgba(40, 50, 75, 0.5);
  outline: none;
}

button.frm-btn {
  border: none;
  height: auto;
  margin: 4px 0;
  font-size: 14px;
  padding: 10px 16px;
  border: 2px solid var(--gold);
  background-color: transparent;
  color: var(--gold);
  width: 95%;
}

.footer-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 4px auto;
  padding: 0;
  max-width: 750px;
}

.footer-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 60px 0 20px;
}

.footer-nav .nav-item {
  font-size: 14px;
  margin: 4px 20px;
  padding: 6px;
}

.thanks {
  font-size: 14px;
  position: relative;
}

.thanks > a {
  color: var(--gold);
  text-decoration: none;
  text-transform: uppercase;
}

@media only screen and (min-width: 50em) {
  footer {
    background-size: contain;
    background-position: right;
    min-height: 320px;
  }

  input {
    margin-right: 18px;
    width: 250px;
  }

  button.frm-btn {
    width: 200px;
  }

  .thanks {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.img-box {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  opacity: 0;
  animation-duration: 0.6s;
  transition: all 0.3s;
}

.img-box.animated {
  z-index: -1;
}

.img-box.animated.zoomIn {
  z-index: 999;
  opacity: 1;
}

.img-box-controls {
  position: absolute;
  bottom: 30px;
  display: flex;
  justify-content: space-between;
  left: 50%;
  height: 50px;
  width: 150px;
  transform: translateX(-50%);
}

.control {
  height: 50px;
  width: 50px;
  border-radius: 20%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.15);
}

.img-box-close {
  position: absolute;
  top: 25px;
  right: 25px;
  height: 50px;
  width: 50px;
  opacity: 0.7;
  transform: scale(0.9);
  transition: all 0.3s;
}

.img-box-close img,
.control img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  height: auto;
}

.control-rgt img {
  transform: rotateY(180deg)  translate(50%, -50%);
}

.img-box-content {
  height: auto;
  width: 95%;
  position: relative;
  object-fit: cover;
}

.img-box-content img {
  width: 100%;
  opacity: 0.95;
  height: auto;
}

.img-box-close:hover,
.control:hover {
  opacity: 1;
  transform: scale(1.2);
}

@media only screen and (min-width: 40em) {
  .img-box-content {
    height: 70vh;
    width: auto;
    max-width: 90%;
    overflow: hidden;
    object-fit: cover;
  }

  .img-box-content img {
    width: auto;
    height: 100%;
  }
}